import { useSelector } from "react-redux";
import { initializeWidget } from "../../components/InitializeWidget";
import { JdiBomPage } from "../../pages";

const JdiBomPageWrapper = () => {
  const loadingParentDetails = useSelector(
    (state) => state.droppedObject.loadingParentDetails
  );

  console.log("loadingParentDetails:", loadingParentDetails);

  return <JdiBomPage objectDropped={loadingParentDetails} />;
};

// Pass the component directly to initializeWidget
const JdiBomWidget = () => initializeWidget(<JdiBomPage />);

export default JdiBomWidget;
